  <script setup>
  import { ArrowRight,Files,Search } from '@element-plus/icons-vue'
  import {getCurrentInstance,onMounted,ref, computed } from 'vue'
  
  const inputValue = ref('');
  const suggestions = ref(['Apple', 'Banana', 'Cherry', 'Date', 'Fig', 'Grape']);
  
  const filteredSuggestions = computed(() => {
    if (!inputValue.value) return [];
    return suggestions.value.filter(suggestion =>
      suggestion.toLowerCase().includes(inputValue.value.toLowerCase())
    );
  });
  
  const onInput = () => {
    // 触发计算属性更新
  };
  
  const selectSuggestion = (suggestion) => {
    inputValue.value = suggestion;
  };
  const instance  = getCurrentInstance() 
  const _this = instance.appContext.config.globalProperties //vue3获取当前this

  onMounted(() => {
    console.log(_this.$route.params)
  })

  function goto(where){
    if(instance != null)
      if (where=='conda') _this.$router.push('/v/conda')
      if (where=='paper') _this.$router.push('/v/paper')
      if (where=='ros') _this.$router.push('/v/ros')
      if (where=='linux') _this.$router.push('/v/linux')
      if (where=='me') _this.$router.push('/i')
      if (where=='hbase') _this.$router.push('/v/hbase')
      if (where=='classes') _this.$router.push('/v/classes')
      if (where=='oge') _this.$router.push('/v/oge')
      if (where=='sparkReadHistory') _this.$router.push('/v/sparkReadHistory')
      if (where=='git') _this.$router.push('/v/git')
      if (where=='Downloads') _this.$router.push('/v/downloads')
    }
  </script>
