import { createRouter,createWebHashHistory } from "vue-router";
import main from "@/components/main.vue";
import interest from "@/components/interest.vue";
import hbase from "@/components/firstLayer/hbase.vue";
import linux from "@/components/firstLayer/linux.vue";
import v from "@/components/v.vue";
import jisuanjiti from "@/components/firstLayer/classes/jisuanjiti.vue";
import shujv from "@/components/firstLayer/classes/shujv.vue";
import wangluo from "@/components/firstLayer/classes/wangluo.vue";
import classes from "@/components/firstLayer/classes.vue";
import paper from "@/components/firstLayer/paper.vue";
import I from "@/components/firstLayer/I.vue";
import imain from "@/components/firstLayer/I/imain.vue";
import cxlmemory from "@/components/firstLayer/paper/cxlmemory.vue";
import intro from "@/components/firstLayer/hbase/intro.vue";
import self from "@/components/firstLayer/I/self.vue";
import oge from "@/components/firstLayer/oge.vue";
import osm from "@/components/firstLayer/oge/osm.vue";
import sparkReadHistory from "@/components/firstLayer/secondlayer/sparkReadHistory.vue";
import git from "@/components/firstLayer/git.vue";
import downloads from "@/components/firstLayer/downloads.vue";




const routes = [
  // {path:'/second',component:second,children:[{path:'',component:dfs}]}
{path:'/',redirect:'/v/main'},
{path:'/interest',component:interest},
{path:'/v',component:v,children:[{path:'hbase',component:hbase},{path:'linux',component:linux},{path:'main',component:main},{path:'classes',component:classes},{path:'paper',component:paper}]},


{path:'/v/hbase/intro',component:intro},

{path:'/v/sparkReadHistory',component:sparkReadHistory},


{path:'/v/git',component:git},

{path:'/v/downloads',component:downloads},


{path:'/v/oge',component:oge},
{path:'/v/oge/osm',component:osm},


{path:'/v/classes/jisuanjiti',component:jisuanjiti},
{path:'/v/classes/wangluo',component:wangluo},
{path:'/v/classes/shujv',component:shujv},


{path:'/v/paper/cxlmemory',component:cxlmemory},


{path:'/i',component:I},
{path:'/i/imain/self',component:self},
{path:'/i/imain/',component:imain}
  ]
  const router = createRouter({
     
    history:createWebHashHistory(),
    routes
  })
  export default router
