<template>
   <el-breadcrumb :separator-icon="ArrowRight" class="topbread">
    <el-breadcrumb-item :to="{ path: '/' }">主页</el-breadcrumb-item>
    <el-breadcrumb-item>身份验证</el-breadcrumb-item>
  </el-breadcrumb>
   <el-row :gutter="200" class="animate__animated animate__fadeInDown">
       <el-col :span="8"  :offset="8">
        <el-form :model="form" label-width="auto" style="max-width: 600px">
    <el-form-item label="password1">
      <el-input v-model="form.name1" /></el-form-item>
    <el-form-item label="password2">
      <el-input v-model="form.name2" /></el-form-item>
    <el-form-item label="password3">
      <el-input v-model="form.name3" />
    </el-form-item>
</el-form>        
 </el-col>
         <el-col :span="8">
           </el-col>
        
</el-row>
  
</template>
<script setup>
import {reactive,onMounted,onUnmounted,getCurrentInstance } from 'vue'
  import { ArrowRight } from '@element-plus/icons-vue'
  const instance  = getCurrentInstance() 
  const _this = instance.appContext.config.globalProperties //vue3获取当前this
var form=reactive({name1:"",name2:"",name3:""})
function login(){
  if(form.name1=="l"&&form.name2=="l"&&form.name3=="l")if (instance != null) _this.$router.push('/i/imain')
  else alert("错误！")
}
    onMounted(() => {
  window.addEventListener('keydown', keyDown)
})
const keyDown = (e) => {
  if (e.keyCode == 13) {
    login()
  }
}
onUnmounted(() => {
  window.removeEventListener('keydown', keyDown, false)
})
</script>
