-基于mesos部署Spark

<div class="f2">Geometry和WKT的区别：</div><br/>
&emsp;&emsp;
表示形式：
Geometry 是空间数据的对象，是几何数据在内存中的表示方式。
WKT 是几何数据的文本格式，是一种标准化的字符串表示。
操作与转换：
Geometry 对象支持空间运算和几何操作，例如计算距离、相交、面积等。
WKT 仅是数据的文本表示，不能直接进行空间运算，通常需要先解析成 Geometry 对象才能进行操作。
用途：
Geometry 用于在程序内部存储和操作几何数据。
WKT 用于数据交换、存储、传输和显示，特别是在数据库和地理信息系统（GIS）中。
