<div class="f2">名词释义：</div><br/>
&emsp;&emsp;序列化（serialization）是对象的一种转化过程，转化后的结构可以在之后再同一个系统或不同的系统中恢复原样（反序列化）（如：json）
&emsp;&emsp;RDD简介P47。RDD用来表示Spark程序中的数据，PySpark中，RDD是由分布在各节点上Python对象组成的，这里的对象可以是列表、元组、字典
等。RDD内部的对象与列表中的元素一样，可以是任何类型的对象，可以是整型、浮点数、字符串，也可以是字典列表元祖，使用Scala或者javaAPI，RDD则分
别由Scala或Java对象组成。
弹性：RDD是有弹性的，意思是Spark中一个执行任务的节点丢失了，数据集依然可以被重建出来。这是因为Spark有每个RDD的谱系，也就是从头构建RDD的步骤
分布式：RDD提供了共享内存以便在不同节点上进程间交换数据
数据集：RDD的各分区




