<template>
     <el-row :gutter="20" class="animate__animated animate__fadeInDown" style="text-align: left;">
        <el-col :span="10"  :offset="7">
    <div class="f2">数据存储模式：</div><br/>
    &emsp;&emsp;在 HBase 中，数据以表的形式存储，每个表由行和列组成。作为一种多版本
存储机制，HBase 存储模型可以认为是一种稀疏的、分布式、持久的多维排序 map，
其中每个存储单元由 Cell 定义，包括 RowKey、ColumnFamily、ColumnQualifier
和 Timestamp,<br/> Cell={ Rowkey，ColumnFamily，ColumnQualifier，Timestamp }
其中，行键（RowKey）是数据库表索引，也是记录的唯一标识符，其可以
是任意字节数组，用来快速定位特定的行。列族（ColumnFamily）是表中的列的
集合，是数据存储和组织的基本单位，包含零个或多个列。列（ColumnQualifier）
是列族中的字段单元。它包含了属性对应的具体数据值。时间戳（Timestamp）
用于表示 Cell 的版本时间，它表示从 1970 年 1 月 1 日开始计算的毫秒数。
HBase 架构体系是其作为分布式存储的核心设计，HBase 架构主要包括
HMaster 与 HRegionServer，其中 HMaster 负责管理整个集群的元数据、调度和
协调工作。而 HRegionServer 作为 HBase 集群中的工作节点，负责存储和处理
数据，每个 HRegionServer 又管理多个表的 Region。值得注意的是， HBase 中
扩展和负载均衡的基本单元是 Region，而 Region 本质上是以行键 RowKey 排序
的连续存储区间。在 HBase 的内置逻辑中，如果 region 太大，系统会动态拆分，
相反如果 region 太小，系统同样会把多个 region 合并，以减少存储的文件数。因
此，合理的预分区策略，可以有效避免 Region 引起的数据存储热点问题。
<div class="f2">数据库行键索引：</div><br/>
&emsp;&emsp;在 HBase 中，数据是作为行为单位存储管理的，每行数据均由一个 RowKey
来进行唯一标识。由于 HBase 本身并不支持二级索引机制，人工构建二级索引需
要在插入数据表时同时插入索引表，因此其主要是通过行键索引来构造数据查询
需求。基于 RowKey 的字典序排列方式，行键索引可以借助于 RowKey 的哈希值
或者范围查询实现快速查找。另外，每个 RowKey 区间存储在不同的 RegionServer
上，分区策略又包括哈希分区、范围分区与随机分区，这使得数据 RowKey 索引
设计以及分区策略将极大影响系统的整体性能
<div class="f2">批量存储：</div><br/>
&emsp;&emsp;对海量数据的分布式管理能够有效支持上层应用分析，HBase 数据存储主要
包括三种方案，分别是 HBase Put，HBase 批量插入，Bulkload 的方案。
HBase Put 方法可以直接通过 HBase API 连接到 HBase 集群，并使用 Table
或 TableBatch 接口进行数据写入操作，在此过程中会发送一次 RPC 请求，并采用序列化方法将 Put 中的数据传送给相应的服务器进行数据插入。API 方式支持
实时写入操作，可以使数据更快地应用于查询。但 API 方式需要在 Spark 分区内
进行调用，大量调用 Put 接口进行数据插入可能会导致大量的 GC 操作。除了影
响性能之外，严重时甚至可能会对 HBase 节点的稳定性造成影响，因此 API 方
式更加适用于小规模数据或实时写入场景。
HBase 批量插入方法即在 HBase Put API 基础上生成一个 List 容器，通过将
分区内多行数据转载到该容器中，在客户端中进行批量提交。其本质是对 List 容
器中的所有对象进行迭代并通过 API 方法执行插入操作，这样的方式会减少
RPC 请求的发送次数。
Bulkload 方式由于利用了 HBase 数据存储在 HDFS 里这一特性，直接在
HDFS 中生成持久化的 HFile 数据格式文件。BulkLoad 不会写 WAL，也不会产
生 Flush 以及 Split，通过配合 MR 完成 HFile 文件生成，然后利用 HBase 提供
Bulkload 工具将 HFile 移动（或加载）到 HBase 目录下。此方案不占用 Region 资
源，不会产生大量的写入 I/O，需要较少的 CPU 和网络资源，因此更加适用于大
规模数据批量写入场景。
在 Spark 中生成 HBase HFile 文件数据需要保持数据整体有序性。HBase Cell
可定义为
Cell Rowkey ColumnFamily ColumnQualifier Timestamp
四元组，因此在调
用 BulkLoad 方法之前需要按照 RowKey、列族、列名(字典顺序)进行排序。最后
将 HFile 加载到目标表目录下。
</el-col>
<el-col :span="8"></el-col></el-row>
</template>
